import { Observable, fromEvent, merge } from 'rxjs';

class Menu {
    private _localePickerButton: HTMLDivElement;
    private _localeCaret: HTMLDivElement;
    private _searchButton: HTMLDivElement;
    private _searchForm: HTMLFormElement;
    private _searchInputField: HTMLInputElement;
    private _localeDropdownComponent: HTMLDivElement;
    private _localeDropdownOption: NodeList;

    private _isLocalePickerVisible: boolean;
    private _isSearchFormVisible: boolean;

    private localePickerObservable$: Observable<Event>;
    private localeDropdownObservable$: Observable<Event>;
    

    init() {
        //#region locale picker
        this._localePickerButton = document.querySelector('.locale-group');
        this._localeCaret = document.querySelector('#navigation .locale-picker');

        this.localePickerObservable$ = fromEvent(this._localePickerButton, 'click');
        this.localePickerObservable$.subscribe((e) => {
            this.toggleLocaleDropdown(e);
        });
        //#endregion

        //#region locale drop down
        this._localeDropdownComponent = document.querySelector('#navigation #locale-dropdown');

        this.localeDropdownObservable$ = new Observable();
        this._localeDropdownOption = document.querySelectorAll('#navigation .option');
        this._localeDropdownOption.forEach((element: HTMLElement) => {
            const click$ = fromEvent(element, 'click');
            this.localeDropdownObservable$ = merge(click$, this.localeDropdownObservable$);
        });

        this.localeDropdownObservable$.subscribe((e) => {
            this.handleDropdownClick(e);
        });
        //#endregion

        //#region search form
        this._searchForm = document.querySelector('#search-form');
        this._searchInputField = document.querySelector('#search-form input');
        this._searchButton = document.querySelector('#navigation .search-link');
        if (this._searchButton) {
            this._searchButton.addEventListener('click', (e) => this.toggleSearchForm(e));
        }
        //#endregion
    }

    get isLocalePickerVisible() {
        return this._isLocalePickerVisible;
    }

    get isSearchFormVisible() {
        return this._isSearchFormVisible;
    }

    private handleDropdownClick(e: Event) {
        const target = e.target as Element;
        location.href = '/' + target.getAttribute('value');
    }

    toggleSearchForm(e?: Event) {
        if (e) {
            e.preventDefault();
            e.stopPropagation();
        }

        if (this._searchForm.classList.contains('hide')) {
            this._searchForm.classList.remove('hide');
            this._searchInputField.focus();
            this._isSearchFormVisible = true;
        } else {
            this._searchForm.classList.add('hide');
            this._isSearchFormVisible = false;
        }   
    }

    toggleLocaleDropdown(e?: Event) {
        if (e) {
            e.preventDefault();
            e.stopPropagation();
        }

        if (this._localeDropdownComponent.style.display === 'none') {
            this._isLocalePickerVisible = true;
            this._localeDropdownComponent.style.display = 'block';
            this._localeCaret.classList.remove('fa-caret-down');
            this._localeCaret.classList.add('fa-caret-up');
            this._localeCaret.classList.add('active');
        } else if (this._localeDropdownComponent.style.display === 'block') {
            this._isLocalePickerVisible = false;
            this._localeDropdownComponent.style.display = 'none';
            this._localeCaret.classList.remove('fa-caret-up');
            this._localeCaret.classList.remove('active');
            this._localeCaret.classList.add('fa-caret-down');
        }
    }
}

export default new Menu();
