const initPagefindUI = () => {
    // eslint-disable-next-line
    // @ts-ignore
    /* webpackIgnore: true */ return new PagefindUI({
        element: ".search-results",
        showSubResults: true,
        pageSize: 10,
        showEmptyFilters: false,
        openFilters: ["Region"],
        ranking: {
            termSimilarity: 10,
            termSaturation: 1,
        },
    });
};

const search = initPagefindUI();

const input = document.querySelector(".search-results form input");
const searchStr = window.location.search || "";
const params = new URLSearchParams(searchStr);
let queryStr = params.get("q");

const url = new URL(window.location.href);

// Update the query params in the URL
input?.addEventListener("input", (e) => {
    if (e?.target instanceof HTMLInputElement) {
        queryStr = e.target.value;
        console.log(queryStr);
        url.searchParams.set("q", queryStr);
        window.history.pushState(null, "", url.toString());
    }
});

// Prefill the search field on load
if (input instanceof HTMLInputElement && queryStr) {
    // TODO: pre-select region filter in sub locale
    // search.triggerFilters({ "Region": [ "NYC", "Marketing" ] });
    search.triggerSearch(queryStr);
}
